import React from 'react';
import img1 from '../../images/12.png'
import './catalog.scss'
function Catalog() {
    return (
        <section className="catalog">
            <h2 className="section-title">   </h2>
            <div className="catalog__container">
                <div className="catalog__img-wrapp">
                    <img className='catalog__img' src={img1} alt="catalog-img" />
                </div>

                <div className="catalog__block">
                    <h3 className="catalog__title">  E-mail,     </h3>
                    <p className="catalog__desc">        </p>
                    <input className='input_e-mail' type="text" placeholder='  E-mail'/>
                    <div className="e-mail__container">
                        <input className='input_e-mail__personal' type="checkbox" name="input_e-mail__personal" id="input_e-mail__personal" />
                        <label className='input_e-mail__personal-label' for="input_e-mail__personal">  <a href="#">  </a></label>
                    </div>
                    <button className='btn-big'> </button>
                </div>
            </div>
        </section>
    );
}

export default Catalog;